body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/*---------------------app-content----*/
.app-content-container{
  display: flex;
  overflow: hidden;
}

.app-locations-view{
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: calc(66% - 1em);
}

.app-map-view{
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: calc(33% - 1em);
  margin-left: 10px;
}

@media screen and (max-width: 1200px) {
  .app-locations-view{
    flex-grow: 0;
    flex-shrink: 1;
    flex-basis: calc(100% - 1em);
  }
  .app-map-view{
    display: none;
  }
}
.locations-view-container{
    padding:10px;
    display: flex;
    flex-direction: column;
}

.locations-view-list-container{
}
  
.locations-view-grid-container{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.locations-view-menu{
    display: flex;
    align-content: flex-end;
    flex-direction: row;
    justify-content: flex-end;
    list-style-type: none;
}
  
.locations-view-menu-item{
    color: #363538;
    cursor: pointer;
    padding:5px 2px 5px 2px;
    margin:0 5px 0 5px;
}
.location-row-container{
    border-bottom: 1px solid #b8c0c0;
    margin: 10px 0 10px 0;
    display: flex;
    flex-direction: row;
    padding: 5px;
}
  
.location-row-name{
    font-weight: bold;
    font-size: 14pt;
    color:black;
    cursor: pointer;
    flex: 1 1 100px;
    line-height: 4em;
}
  
.location-row-description{
    font-size: 10pt;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 1.3em;
    height:5.2em;
    flex: 5 1;
}
  
.location-row-attractivness{
    display: flex;
    justify-content: center;
    flex: 0.5 1;
    line-height: 5em;
}
  
.location-row-image{
    max-width: 80px;
    max-height: 80px;
    flex: 1 1 50px;
}
  
.location-row-buttons{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
  
.location-row-button{
    cursor: pointer;
}
.location-card{
    display: flex;
    flex-direction: column;
    flex-grow: 0;
    flex-shrink: 1;
    flex-basis: calc(100% - 1em);
    border: 1px solid lightgrey;
    margin-bottom: 10px;
    padding: 5px;
}
  
.location-card-description{
    font-size: 10pt;
    overflow: hidden;
    max-height: 100px;
    flex: 3 1;
}
  
.location-card-name{
    font-weight: bold;
    cursor: pointer;
    flex: 1 1;
}
  
.location-card-image{
    width:100%;
    max-height:100px;
    flex: 2 1;
}

@media screen and (min-width: 700px) {
    .location-card {
        flex-grow: 0;
        flex-shrink: 1;
        flex-basis: calc(50% - 1em);
    }
}
  
@media screen and (min-width: 1000px) {
    .location-card {
        flex-grow: 0;
        flex-shrink: 1;
        flex-basis: calc(33% - 1em);
    }
}
  
@media screen and (min-width: 1200px) {
    .location-card {
        flex-grow: 0;
        flex-shrink: 1;
        flex-basis: calc(25% - 1em);
    }
}
  
.location-card-bottom-container{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    border-top: 1px solid lightgray; 
    flex: 1 1; 
}

/*----------grid buttons----------*/

.location-card-buttons-container{
    display: flex;
    justify-content: space-around;
    width:100%;
    border-top: 1px solid lightgray;
    padding-top: 5px;
}
  
.location-card-button{
    border: 0;
    background: none;
    box-shadow: none;
    border-radius: 0px;
    cursor: pointer;
    font-size: 12pt;
    flex: 1 1;
}

.location-card-button-left{
    border-right: 1px solid lightgray;
}
  
.location-card-button:after{
    border-left: 1px solid lightgray;
}
.location-attractivness-container{
    display: flex;
    justify-content: center;
    flex-flow: row-reverse;
    width: 100%;
}

.location-attractivness-star, 
.location-attractivness-star-selected{
    font-size: 20pt;
    cursor: pointer;
}
  
.location-attractivness-star:hover, 
.location-attractivness-star:hover ~ .location-attractivness-star, 
.location-attractivness-star-selected, 
.location-attractivness-star-selected-inactive {
    color: #f58a07;
}

.location-attractivness-star-inactive, 
.location-attractivness-star-selected-inactive{
    font-size: 20pt;
    cursor: default;
}
.modal-background{
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    z-index: 9999;
}
  
.modal{
    position: relative;
    max-width: 600px;
    background: white;
    top: 30%;
    left: 50%;
    -webkit-transform: translate(-50%,-30%);
            transform: translate(-50%,-30%);
    display: flex;
    flex-direction: column;
    box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
}

@media screen and (max-width: 700px) {
    .modal{
        max-width: 300px;
    }
}

@media screen and (max-height: 400px) {
    .modal{
        max-height: 100%;
    }
}
  
.modal-header{
    padding: 10px;
    padding-left: 30px;
    font-size: 16pt;
    font-weight: bold;
    color: black;
    flex: 1 1;
}

.modal-header-container{
    width:100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.modal-header-close-button{
    cursor: pointer;
    color: gray;
    margin-right: 5px;
}
  
.modal-content{
    padding: 15px;
    font-size: 12pt;
    color: gray;
    text-align: left;
    overflow-y: auto;
    flex:10 1;
}
  
.modal-footer{
    padding: 10px;
    font-size: 20pt;
    color: black;
    background: white;
    width: 100%;
    flex:1 1;
}
.location-edit-form-container
{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.location-edit-form-row
{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-content: flex-start;
    margin-top: 10px;
}

.location-edit-form-item
{
    display: flex;
    flex-direction: column;
    flex: 1 1;
    margin: 0 5px 0 5px;
}

.location-edit-form-buttons{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin: 0 5px 0 5px;
}

.location-edit-form-buttons > button{
    margin: 0 5px 0 5px;
}

@media screen and (max-width: 700px) {
    .location-edit-form-row
    {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-content: flex-start;
    }

    .location-edit-form-buttons
    {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    .location-edit-form-buttons > button {
        margin-top: 5px;
    }
}

.location-form-error{
    color: red;
    text-align: center;
}

.search-result-container{
    display: flex;
    flex-direction: column;
}

.search-result-item{
    cursor: pointer;
    padding-left: 70px;
    height: 30px;
}

.search-result-error{
    color: red;
    padding-left: 70px;
}
.header{
    background-color: #363538;
    color: #f7fff6;
    height: 40px;
    text-align: left;
    padding: 10px;
  }
  
.locations-map-view{
    height:750px;
    width:100%;
}

.location-form-map-view{
    height:300px;
    width: 500px;
}

@media screen and (max-width: 700px) {
    .location-form-map-view{
        display: none;
    }
}
.add-new-location{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.add-new-location > button{
    margin: 5px;
}
